
import { AjaxHttp, AjaxImageHttp } from '../../../js/ajaxHttp'
import { getHMFormateTime, isArrayType, compressImage, createBlob, fileSize } from "../../../js/utility.js"
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import { mapState } from 'vuex'
import event from '../../../js/Event'
import $ from 'jquery'
import { PLATFORM } from '../../../config.js'

export default {
name: 'createsign',
components: {
Group,
Cell,
XTextarea,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
data() {
return {
content: '',
files: [],
}
},
computed: {
...mapState([
'detail',
'detailSecond',
]),
smalltime() {
if (this.detail === "" || this.detail.currentTime === "") { return ""; }
return getHMFormateTime(this.detail.currentTime);
},
position() {
if (this.detail !== "" && this.detail.address.wktype === 'poi') {
return this.detail.address.address + this.detail.address.title;
} else if (this.detail !== "" && this.detail.address.wktype === 'defalut') {
return this.detail.address.address;
}
return "";
},
customerName() {
if (this.detail === "") { return ""; }
return this.detail.customerName;
},
},
mounted() {},
methods: {
getList() {
AjaxHttp('POST', url, postParams, this, function(res, wself, success) {



});
},
onSwipeoutClick(type, item, index) { //edit delete
if (type === 'delete') {}
},
addFiles() {
if (PLATFORM === 'web') {
if ($(fileElem)) {
$(fileElem).click();
}
} else {
navigator.camera.getPicture(onSuccess, onFail, {
quality: 50,
sourceType: Camera.PictureSourceType.CAMERA,
destinationType: Camera.DestinationType.FILE_URI
});

var self = this;

function onSuccess(imageURI) {
window.resolveLocalFileSystemURL(imageURI, function success(fileEntry) {



fileEntry.file(function(file) {
self.updateNativeImage(file, fileEntry.name);
}, function(message) {
alert('!');
});
}, function() {




















});
}

function onFail(message) {

}
}
},
uploadimg(e) {
var src, url = window.URL || window.webkitURL || window.mozURL,
files = e.target.files;
var self = this;
for (var i = 0, len = files.length; i < len; ++i) {
var file = files[i];
compressImage(file, 0.3, (code) => {
var blob = createBlob(code);
self.files.push({ 'name': file.name, 'size': fileSize(blob.size), 'file_path': code, 'blob': blob, 'file_type': 'jpg', 'local': true });
});
}
},
updateNativeImage(file, name) {
var self = this;
compressImage(file, 0.3, (code) => {
var blob = createBlob(code);
self.files.push({ 'name': file.name, 'size': fileSize(blob.size), 'file_path': code, 'blob': blob, 'file_type': 'jpg', 'local': true });
});

},
fileTypeImage(item) {
var type = item.file_type;
if (type === 'doc' || type === 'DOC' || type === 'docx') {
return require("../../../assets/file/doc.png");
} else if (type === 'xls' || type === 'xlsx') {
return require("../../../assets/file/excel.png");
} else if (type === 'png' || type === 'gif' || type === 'jpg' || type === 'jpeg') {
if (item.local === true) {
return item.file_path;
} else {
return getImageUrl(item.file_path);
}
} else if (type === 'pdf' || type === 'PDF') {
return require("../../../assets/file/pdf.png");
} else if (type === 'ppt' || type === 'PPT' || type === 'pptx') {
return require("../../../assets/file/ppt.png");
} else if (type === 'txt' || type === 'TXT') {
return require("../../../assets/file/txt.png");
} else if (type === 'zip' || type === 'ZIP' || type === 'rar') {
return require("../../../assets/file/zip.png");
}
return require("../../../assets/file/unknown.png");
},









previewerImg(images, imgindex) {
var previewerlist = [];
for (var i = 0; i < images.length; i++) {
var item = images[i];
previewerlist.push({
src: item.file_path
});
}
event.$emit('previewerAction', { list: previewerlist, index: imgindex });
},
submite() {
if (this.detail === "") { return; }

var formdata = new FormData();
formdata.append('customer_id', this.detail.customerID);
formdata.append('x', this.detail.address.point.lat);
formdata.append('y', this.detail.address.point.lng);

if (this.detail.address.wktype === 'poi') {
formdata.append('address', this.detail.address.address);
formdata.append('title', this.detail.address.title);
} else if (this.detail.address.wktype === 'defalut') {
formdata.append('address', this.detail.address.addressComponents.province + this.detail.address.addressComponents.city + this.detail.address.addressComponents.district + this.detail.address.addressComponents.street);
formdata.append('title', this.detail.address.addressComponents.streetNumber);
}
formdata.append('log', this.content);

for (var i = 0; i < this.files.length; i++) {
var item = this.files[i];
formdata.append('img[]', item.blob, item.name);
}































AjaxImageHttp('POST', 'm=sign&a=add', formdata, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
if (wself.detailSecond !== '' && wself.detailSecond.type === 'teamsign') { // 
wself.detailSecond.refresh = true;
wself.$store.commit('updateDetailSecond', wself.detailSecond);
}
wself.$router.go(-1);
}
});
}
}
}




